
.header {
  background: radial-gradient(ellipse at 50% 60%, rgba(255, 255, 255, 0), black), url("../assets/moreSky.jpg");
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100vh;
  width: 100%;
  padding: 40px;
}
.header #clouder {
    height: 300px;
    width: 300px;
}
.header__main-banner {
    text-shadow: 2px 3px 10px black;
    -webkit-transform: translateY(-15%);
            transform: translateY(-15%);
    font-family: 'Raleway', sans-serif;
    font-size: 64px;
    font-weight: 900;
    z-index: 99;
    -ms-flex-item-align: center;
        -ms-grid-row-align: center;
        align-self: center;
    color: #FFF;
    top: 50%;
    width: 60%;
}
.header .great {
    text-overflow: nowrap;
    font-size: 100%;
    color: #20BF55;
}
.header canvas.particles-js-canvas-el {
    /* background-color: rgba(#4781FF,0.2); */
    position: absolute;
    top: 0;
    left: 0;
}



